import React, { Component } from 'react';
import MainPage from './MainPage';

class App extends Component {
  render() {
    return (
      <div>
        <h3>react-url-query example: react-router-v2-and-redux</h3>
        <MainPage/>
      </div>
    );
  }
}

export default App;



